.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: 'Raleway', sans-serif;
  }
  
  .navbar {
    display: flex;
    flex-direction: row;
    box-shadow: 0.5px 0.5px 6px 0px rgba(121, 120, 120, 0.342);
    background-color: white;
    margin-top: 0;
    width: 100%;
    height: 55px;
    border-radius: 7px 7px 0px 0px;
  
  }
  
  .navbar>p {
    font-size: 12px;
    font-weight: 500;
    margin-left: 41%;
    margin-top: 18px;
  }
  
  .app-question {
    background-color: rgba(243, 240, 240, 0.671);
    width: 1100px;
    min-height: 520px;
    height: min-content;
    border-radius: 7px;
    box-shadow: 3px 3px 12px 0px rgba(51, 50, 50, 0.603);
    justify-content: space-evenly;
  }
  
  
  .question-number {
    font-size: 15px;
    font-weight: 500;
    color: rgb(63, 185, 63);
    margin-left: 45%;
    margin-top: 35px;
  }
  
  .question {
    font-size: 34px;
    font-weight: 700;
    margin-right: 10%;
    margin-top: 30px;
    text-align: center;
  }
  
  .anwser {
    display: flex;
    flex-direction: row;
  }
  
  .div1-question02,
  .div2-question02 {
    width: 250px;
    height: 150px;
    margin-top: 2%;
    margin-left: 65%;
    box-shadow: 2px 2px 6px 0px rgba(136, 135, 135, 0.877);
    border-radius: 7px;
    cursor: pointer;
    background-color: white;
  }
  
  .div1-question02>p,
  .div2-question02>p {
    color: rgb(63, 185, 63);
    font-size: 23px;
    text-align: center;
    padding-top: 17%;
  
  }
  
  .div1-question02:hover,
  .div2-question02:hover {
    transform: scale(1.1);
    transition: 400ms linear;
  
  }
  
  
  .link1 {
    text-decoration: none;
    color: black;
    margin-left: 7%;
    margin-top: 6%;
  }