.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: 'Raleway', sans-serif;
}

.navbar{
  display: flex;
  flex-direction: row;
  box-shadow: 0.5px 0.5px 6px 0px rgba(121, 120, 120, 0.342);
  background-color: white;
  margin-top: 0;
  width: 100%;
  height: 55px;
  border-radius: 7px 7px 0px 0px;

}

.navbar > p {
  font-size: 12px;
  font-weight: 500;
  margin-left: 41%;
  margin-top: 18px;
}

.app-explicacao{
background-color: white;
width: 1100px;
min-height: 520px;
height: min-content;
border-radius: 7px;
box-shadow: 3px 3px 12px 0px rgba(51, 50, 50, 0.603);
justify-content: space-evenly;
}

.errado {
  width: 200px;
  height: 60px;
  box-shadow: 3px 3px 7px 0px rgba(119, 117, 117, 0.603);
  background-color: white;
  border-radius: 10px;
  margin-left: 40%;
  margin-top: 2.5%;
}


.errado >p {
  font-weight: 700;
  color: red;
  padding-top: 16px;
  padding-left: 42px;
  font-size: 26px;
}

.explicacao  {
  display: flex;
  flex-direction: column;
  width: 550px;
  margin-left: 24%;
  padding: 10px;
}

.explicacao-titulo {
  font-weight: 700;
  text-align: center;
  font-size: 18px;
  margin-bottom: 2px;
}

.explicacao-texto{
  font-size: 15px;
  font-weight: 500;
  text-align: center;
  margin-bottom: 7%;
}

.link1 {
  text-decoration: none;
  color: black;
  margin-left:7%;
  margin-top:6%;
}

.proxima{
  width: 150px;
  height:45px;
  background-color: rgba(89, 221, 89, 0.959);
  border-radius: 20px;
  margin-left: 42%;
  box-shadow: 1px 1px 2px 0.5px rgba(51, 50, 50, 0.507);
  cursor: pointer;
  border: none;
 }

 .proxima:hover{
  background-color: rgba(123, 228, 123, 0.959);
 }

.proxima > p {
font-weight: 600;
font-style: bold;
color:  white;
font-size: 22px;
padding-top: 8px;
margin-left: 27px;
}